* {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    font-family: Georgia, 'Times New Roman', Times, serif;
}


header {
    padding-top: 50px;
    padding-bottom: 50px;
}

header h1 {
    color: #AAA;
    font-size: 36px;
    font-weight: 400;
    text-align: center;
}

header h1 strong {
    color: #313131;
    font-style: italic
}

.header{
    text-align: center;
}
.content-wrap {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    flex-wrap: wrap;
    -webkit-box-pack: center;
    justify-content: center;
    gap: 40px;
    padding: 2rem;
}

.content-wrap aside {
    -webkit-box-flex: 1;
    -ms-flex: 1 1 250px;
    flex: 1 1 250px;
    max-width: 250px;
    padding-right: 16px;
    width:20vw
}

.content-wrap aside nav {
    padding-left: 8px;
    padding-right: 8px;
    background-color: lightblue;
    border-radius: 10px;
}

.content-wrap aside nav h3 {
    color: #313131;
    font-size: 24px;
    margin-bottom: 16px;
}

.content-wrap aside nav a {
    display: flex;
    justify-content: space-between;
    text-align: center;
    text-align: left;
    font-size: 16px;
    margin-bottom: 16px;
    text-decoration: none;
    -webkit-transition: 0.4s;
    transition: 0.4s;
    color:#34568B
}

.content-wrap aside nav a:hover {
    transform: translateY(-2px);
    box-shadow: 0 2px 40px 0 hsl(0 0% 0% / 0.1);
}

.content-wrap main {
    -webkit-box-flex: 1;
    -ms-flex: 1 1 0%;
    flex: 1 1 0%;
    padding: 0px 16px;
}

.content-wrap main .main-head {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: end;
    -ms-flex-pack: end;
    justify-content: flex-end;
}

.content-wrap main .main-head .search-box {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: end;
    -ms-flex-pack: end;
    justify-content: flex-end;
    -webkit-box-flex: 1;
    -ms-flex: 1 1 0%;
    flex: 1 1 0%;
}

.content-wrap main .main-head .search-box input {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background: none;
    outline: none;
    border: none;
    display: block;
    width: 100%;
    max-width: 400px;
    padding: 16px;
    border-radius: 999px;
    background-color: #EEE;
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

.content-wrap main .main-head .search-box input ::-webkit-input-placeholder {
    color: #888;
}

.content-wrap main .main-head .search-box input :-ms-input-placeholder {
    color: #888;
}

.content-wrap main .main-head .search-box input ::-ms-input-placeholder {
    color: #888;
}

.content-wrap main .main-head .search-box input ::placeholder {
    color: #888;
}

.content-wrap main .main-head .search-box input:focus,
.content-wrap main .main-head .search-box input:valid {
    background-color: #313131;
    color: #FFF;
}

.content-wrap main .manga-list {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-top: 32px;
    margin: 32px -8px 0px;
}

.content-wrap main .manga-list .manga-card {
    -webkit-box-flex: 1;
    -ms-flex: 1 1 33.333%;
    flex: 1 1 33.333%;
    max-width: 33.333%;
    padding: 16px 8px;
}

.content-wrap main .manga-list .manga-card a {
    text-decoration: none;
}

.content-wrap main .manga-list .manga-card figure {
    display: block;
    margin-bottom: 16px;
}

.content-wrap main .manga-list .manga-card figure img {
    display: block;
    width: 100%;
    height: 350px;
    -o-object-fit: cover;
    object-fit: cover;
    border-radius: 16px;
    -webkit-box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.15);
    box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.15);
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

.content-wrap main .manga-list .manga-card h3 {
    color: #34568B;
}

.content-wrap main .manga-list .manga-card:hover figure img {
    -webkit-box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.25);
    box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.25);
    -webkit-transform: scale(1.05);
    transform: scale(1.05);
}

button {
    color: #333;
    border: 1px solid #5e5e5e;
    padding: 4px 12px;
    border-radius: 5px;
    font-size: 0.9rem;
    font-weight: 600;
    transition: all 150ms;
    width: 5rem;
    cursor: pointer;
  }
  
  button:hover {
    transform: translateY(-2px);
    box-shadow: 0 2px 3px 0 hsl(0 0% 0% / 0.1);
  }